.posts {
  display: flex;
  flex-wrap: wrap;
  gap: 10%;
  a, a:hover {
    text-decoration: none;
  }
  h3 {
    margin: 0;
  }
  .post {
    background-color: #04212d;
    border-radius: 16px;
    display: flex;
    flex-direction: column;
    margin-top: 40px;
    overflow: hidden;
    @media (min-width: 720px) {
      width: 45%;
    }
  }
  .summary-container {
    padding: 0 16px 12px 16px;
    opacity: 0.80;
  }
  .title {
    padding: 12px 16px 0 16px;
  }
  img {
    height: 100%;
    margin: 0;
    width: auto;
  }
  .main-image {
    flex: 0 0 240px;
    object-fit: cover;
    object-position: center 20%;
  }
  .byline {
    padding: 16px 16px 0 16px;
    opacity: 0.80;
  }
}
